@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
*{box-sizing:border-box}body{margin:0;font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#fff;color:#2C2C2C}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.wrapper{max-width:1826px;width:100%;margin:0 auto;padding:0 10px}.row{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}a{text-decoration:none}h1{font-size:36px;font-weight:700}h3{font-size:24px;font-weight:500}h5{font-size:18px;font-weight:700;margin:0}p{font-size:16px;line-height:24px}button{background:none;border:none;cursor:pointer}

.card{width:33.333333%;margin:17px;background:#F8FAFC;border-radius:25px;transition:300ms;padding:45px}.card:hover{box-shadow:0 4px 45px #C3D4E4}.card:hover .header__img{-webkit-transform:matrix(2, -0.5, 0.6, 1.4, 0, 0);transform:matrix(2, -0.5, 0.6, 1.4, 0, 0)}.card__header{position:relative;text-align:center}.card .header__state{position:absolute;top:0;left:-15px;background:linear-gradient(261.65deg, #EFBB37 -3.95%, #CF9E1F 109.44%);box-shadow:0 0 15px rgba(0,0,0,0.1);border-radius:30px;font-size:14px;font-family:SF UI Display Medium, sans-serif;color:#ffffff;text-transform:uppercase;padding:4px 20px;line-height:17px}.card .header__compare{position:absolute;top:-10px;right:-15px;background:#ffffff;box-shadow:0 0 15px rgba(0,0,0,0.1);border-radius:30px;padding:15px;cursor:pointer;transition:200ms}.card .header__compare:hover{box-shadow:0 0 8px #BDD0E3;background:#F9FAFC}.card .header__block{overflow:hidden;height:261px;width:250px;margin:0 auto}.card .header__img{height:261px;transition:200ms}.card .header__title{text-align:center;font-family:SF UI Display Medium, sans-serif;margin-bottom:0}.card .header__text{margin-top:10px;font-family:SF UI Display Light, sans-serif}.card .info{position:relative;text-align:end}.card .info__colors{position:absolute;top:0;left:0;background:#ffffff;border-radius:15px;width:258px;cursor:pointer;padding:3px 0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;z-index:2}.card .info__header{position:relative;width:100%}.card .info__arrow{position:absolute;top:50%;right:20px;-webkit-transform:translateY(-50%);transform:translateY(-50%);transition:200ms}.card .info__arrow.active{-webkit-transform:rotate(180deg);transform:rotate(180deg)}.card .info__title{font-size:12px;line-height:14px;font-family:SF UI Display Medium, sans-serif;padding:5px 20px;width:100%;text-align:start}.card .info__option:hover{background:#F3F8FC;border-radius:0 10px 10px 0}.card .info__select{height:0;-webkit-transform:scaleY(0);transform:scaleY(0);-webkit-transform-origin:0 0;transform-origin:0 0;overflow-y:auto;width:100%;transition:200ms}.card .info__select::-webkit-scrollbar{width:3px;background-color:#ffffff}.card .info__select::-webkit-scrollbar-thumb{background-color:#E0E9F2;border-radius:15px}.card .info__select.active{height:75px;-webkit-transform:scaleY(1);transform:scaleY(1)}.card .form{margin-top:28px}.card .form .container{display:block;position:relative;padding-left:30px;margin:14px 20px;font-size:16px;line-height:19px;font-family:SF UI Display Medium, sans-serif}.card .form .container input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.card .form .container input:checked ~ .checkmark:after{content:'';display:block}.card .form .container input:checked ~ .checkmark:before{content:'';width:17px;height:17px;background:linear-gradient(261.65deg, #EFBB37 -3.95%, #CF9E1F 109.44%);border-radius:5px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.card .form .container .checkmark{position:absolute;top:0;left:0;height:22px;width:22px;background-color:#DEDEDE;border-radius:5px}.card__buttons{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin-top:28px}

.btn{background:linear-gradient(261.65deg, #EFBB37 -3.95%, #CF9E1F 109.44%);border-radius:30px;font-family:SF UI Display Semibold, sans-serif;color:#ffffff;text-transform:uppercase;font-size:18px;padding:9px 115px;margin-left:30px;transition:300ms}.btn:hover{background:linear-gradient(261.65deg, #E9B32C -3.95%, #BF911A 109.44%)}.btn:disabled{background:linear-gradient(261.65deg, #FFD773 -3.95%, #E2B645 109.44%)}.btns{font-size:20px;background:#E2E9F0;border-radius:30px;width:92px;transition:200ms;margin-left:16px}.btns:hover{box-shadow:0 0 15px rgba(0,0,0,0.1)}.btns__select{padding:13px 15px;font-size:16px}

.nav{background:#ffffff;box-shadow:0 0 10px 5px #C3D4E4;padding:10px 0}.nav__logo{font-size:24px;color:gray}.nav__block{position:relative}.nav__img{height:24px}.nav__img:nth-child(1){margin-right:20px}.nav__counter{position:absolute;top:-17px;right:10px;color:#ffffff;background:linear-gradient(261.65deg, #EFBB37 -3.95%, #CF9E1F 109.44%);border-radius:30px;padding:3px 6px;font-size:11px}

.cart{width:100%;margin-top:10px}.cart__product{background:#F8FAFC;border-radius:25px;padding:34px 65px;margin:6px 0}.cart__block{width:110px;margin-right:69px;text-align:center}.cart__img{height:109px}.cart__title{display:inline-block}.cart__item{display:inline-block;font-weight:400;padding:0 60px}.cart__btn{padding:0 60px}.cart__icon{margin:0 60px;width:24px}

.compare{width:100%;margin-top:10px}.compare__product{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.compare__left{width:200px}.compare__block{margin:0 50px;width:260px;background:#ffffff;border-radius:25px;text-align:center;box-shadow:0 4px 45px #C3D4E4}.compare__img{height:265px}.compare__text{margin:0 50px;width:260px;text-align:center}

